import { RendererThree } from './base/RendererThree';
import { getCheckBox } from './base/util'

export class Solution {
    div: HTMLDivElement
    renderer: RendererThree;
    m: number[] = [0, 0];
    t: number = 0.0;
    pts: Point[] = [];
    constructor(id: string) {
        this.div = document.getElementById(id) as HTMLDivElement;
        this.renderer = new RendererThree(id);
        this.renderer.render = (scene) => this.render(scene);
        this.initDom();
        this.init();
    }
    public initDom() {
        const div = document.createElement('div');
        div.style.position = 'absolute'
        this.div.appendChild(div);

        const cBox = getCheckBox('grid');
        div.append(cBox.div);
        cBox.checkBox.checked = true;
        cBox.checkBox.onchange = () => {
            if (cBox.checkBox.checked === true) {
                this.renderer.showGrid = true;
            } else {
                this.renderer.showGrid = false;
            }
        }

        const axis = getCheckBox('axis');
        div.append(axis.div);
        axis.checkBox.checked = true;
        axis.checkBox.onchange = () => {
            if (axis.checkBox.checked === true) {
                this.renderer.showAxis = true;
            } else {
                this.renderer.showAxis = false;
            }
        }
    }
    public init() {


        for(let i = -10; i < 25; i++) {
            for(let j = -10; j < 10; j++) {
                const p = new Point(i ,j ,0);
                this.pts.push(p);
                this.renderer.addPoint(p)
            }
        }

        // console.log('init')
    }
    public render(scene): void {
        // console.log(scene)

        // this.pts[0].vec.z-= 0.0001
        // ctx.fillStyle = '#f00';


        for(let i = 0 ; i < this.pts.length; ++i) {
            this.pts[i].vec.z = Math.cos(i * this.t * 0.01)
        }


        // this.pts.forEach((v)=>{
        //     v.move(this.t, 0.7)
        //     v.render(ctx);
        // });

        // this.renderMouse(ctx);
        this.t +=0.01
    }
    public mouseMove(x: number, y: number): void {
        // this.m[0] = x - this.canvas.width * 0.5;
        // this.m[1] = y - this.canvas.height * 0.5;
    }
    public renderMouse(ctx) {
        // ctx.fillStyle = '#eee';
        // ctx.beginPath();
        // ctx.arc(this.m[0], this.m[1], 3, 0, Math.PI * 2.0)
        // ctx.closePath();
        // ctx.fill();
    }
    public mouseClick(x: number, y: number): void {
        // const v = new Point(x - this.canvas.width * 0.5, y - this.canvas.height * 0.5, 0);
        // this.pts.push(v);

    }
    public destroy() {
        this.renderer.destroy();
    }
}

class Point {
    public vec: Vector3;
    public velocity: Vector3;
    public force: Vector3;
    public mass = 10;
    public r: number = 10;

    constructor(x, y, z) {
        this.vec =      new Vector3(x, y, z)
        this.velocity = new Vector3(0,0,0)
        this.force =    new Vector3(0.0, -0.1,0)

    }
    public render(ctx: CanvasRenderingContext2D) {
        ctx.beginPath();
        ctx.arc(this.vec.x, this.vec.y, this.r, 0, Math.PI * 2.0)
        ctx.closePath();
        ctx.fill()
    }
    public move(dt: number, damping: number = 0.9) {

        // u *= damping;
        // u += f * (dt / m);
        // p += u * dt;

        this.velocity = multiply(this.velocity, damping);
        this.velocity = add(this.velocity,  multiply(this.force, (dt / this.mass )))

        this.vec = add(this.vec, multiply(this.velocity, dt));

        // if (this.vec.y < 0) {
        //     this.vec.y *= -1.0
        // }
    }
}


// ......................
const xAxis = () => {
    return new Vector3(1, 0, 0);
}
const yAxis = () => {
    return new Vector3(0, 1, 0);
}
const zAxis = () => {
    return new Vector3(0, 0, 1);
}
const zero = () => {
    return new Vector3(0, 0, 0);
}
const origin = () => {
    return new Vector3(0, 0, 0);
}
// ......................

const distance = (v0: Vector3, v1: Vector3) => {
    return Math.sqrt(distance2(v0, v1));
}
const distance2 = (v0: Vector3, v1: Vector3) => {
    return (v0.x - v1.x) * (v0.x - v1.x) + (v0.y - v1.y) * (v0.y - v1.y) + (v0.z - v1.z) * (v0.z - v1.z);
}
const dotProduct = (v1: Vector3, v2: Vector3) => {
    return v1.x * v2.x + v1.y * v2.y + v1.z * v2.z;
}
const crossProduct = (v1: Vector3, v2: Vector3) => {
    return new Vector3(
        v1.y * v2.z - v1.z * v2.y,
        v1.z * v2.x - v1.x * v2.z,
        v1.x * v2.y - v1.y * v2.x
    );
}
const normalize = (v: Vector3) => {
    let len: number = Math.sqrt(v.x * v.x + v.y * v.y + v.z * v.z);
    if (len < 0.0001) {
        return new Vector3(v.x, v.y, v.z);
    }
    return new Vector3(v.x / len, v.y / len, v.z / len);
}
const length = (v: Vector3) => {
    return Math.sqrt(v.x * v.x + v.y * v.y + v.z * v.z);
}

// ......................
const multiply = (v: Vector3, s: number) => {
    return scale(v, s);
}
const scale = (v: Vector3, s: number) => {
    return new Vector3(v.x * s, v.y * s, v.z * s);
}
const sub = (v0: Vector3, v1: Vector3) => {
    return new Vector3(v0.x - v1.x, v0.y - v1.y, v0.z - v1.z);
}
const add = (v0: Vector3, v1: Vector3) => {
    return new Vector3(v0.x + v1.x, v0.y + v1.y, v0.z + v1.z);
}

