import * as THREE from 'three';
import { OrbitControls } from 'three-orbitcontrols-ts';

class RendererThreeBase {
    public canvas: HTMLCanvasElement;
    public hostDiv: HTMLElement;
    public ctx: CanvasRenderingContext2D;
    public t: number = 0.0;
    public camera: THREE.PerspectiveCamera;
    public scene: THREE.Scene;
    public renderer: THREE.WebGLRenderer;
    public controls: OrbitControls;
    private requestFrame: number;
    constructor(id: string) {
        this.hostDiv = document.getElementById(id);
        this.initCanvas();
    }
    private initCanvas() {
        THREE.Object3D.DEFAULT_UP.set(0, 1, 0);
        this.scene = new THREE.Scene();
        this.scene.background = new THREE.Color(0x000000);
        this.scene.castShadow = true;

        this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
        this.renderer.setPixelRatio(this.hostDiv.clientWidth / this.hostDiv.clientHeight);
        this.renderer.domElement.id = 'Three';
        this.renderer.domElement.style.position = 'absolute';
        this.renderer.shadowMap.enabled = true;
        this.renderer.setSize(this.hostDiv.clientWidth, this.hostDiv.clientHeight);
        this.hostDiv.appendChild(this.renderer.domElement);

        this.camera = new THREE.PerspectiveCamera(30, this.hostDiv.clientWidth / this.hostDiv.clientHeight, 1, 5000);
        this.controls = new OrbitControls(this.camera, this.renderer.domElement);
        this.camera.position.set(20, 20, 20);
        this.controls.update();

        this.canvas = this.renderer.domElement;

        this.init();
    }
    private init() {
        this.eventBind();

    }
    private eventBind() {
        this.renderer.domElement.onmousedown = (e: MouseEvent) => this.mouseDown(e);
        this.renderer.domElement.onmouseup = (e: MouseEvent) => this.mouseUp(e);
        this.renderer.domElement.onmousemove = (e: MouseEvent) => this.mouseMove(e);
        this.renderer.domElement.addEventListener('click', (e: MouseEvent) => this.mouseClick(e))

        window.addEventListener('resize', () => this.onWindowResize());

    }
    private onWindowResize() {
        this.camera.aspect = this.hostDiv.clientWidth / this.hostDiv.clientHeight;
        this.camera.updateProjectionMatrix();
        this.renderer.setSize(this.hostDiv.clientWidth, this.hostDiv.clientHeight);
    }
    private loop = () => {
        this.requestFrame = requestAnimationFrame(this.loop);
        this.controls.update();
        this.update(this.scene);
        this.renderer.render(this.scene, this.camera);
        this.t += 0.01;

        // console.log('rendering...');
    }
    // .............................................................................
    public start() {
        this.loop();
    }
    public mouseDown(e: MouseEvent) {
        // console.log(e);
    }
    public mouseUp(e: MouseEvent) {
        // console.log(e);
    }
    public mouseClick(e: MouseEvent) {
        // console.log(e);
    }
    public mouseDClick(e: MouseEvent) {
        // console.log(e);
    }
    public mouseMove(e: MouseEvent) {
        // console.log(e);
    }
    public mouseDrag(e: MouseEvent) {
        // console.log(e);
    }
    public update(scene: THREE.Scene) {

    }
    public destroy() {
        window.removeEventListener('resize', () => this.onWindowResize());
        cancelAnimationFrame(this.requestFrame);
        this.requestFrame = undefined;
        while (this.hostDiv.lastChild) {
            this.hostDiv.removeChild(this.hostDiv.lastChild);
        }
    }
}


export class RendererThree extends RendererThreeBase {

    pointLight: THREE.PointLight

    raycaster = new THREE.Raycaster();
    mouse = new THREE.Vector2();
    m = new THREE.Vector3();

    sphere: THREE.Mesh;
    
    ground: THREE.Mesh;

    axis: THREE.AxesHelper;
    grid: THREE.GridHelper;

    arrayMesh: THREE.Mesh[] = [];
    arrayPoint: any = [];
    arrayArrow: any = [];

    constructor(id: string = 'main') {
        super(id)
        this.initPost();
        this.start();
    }
    public initPost() {
        const ambientLight = new THREE.AmbientLight(0x555555);
        this.scene.add(ambientLight);

        this.pointLight = new THREE.PointLight(0xffffff, 100);
        this.pointLight.position.x = 10;
        this.pointLight.castShadow = true;
        this.scene.add(this.pointLight);

        this.axis = new THREE.AxesHelper(5);
        this.axis.position.y = 0.001;
        this.scene.add(this.axis);

        const sizeGrid = 50
        this.grid = new THREE.GridHelper(sizeGrid, 100, 0x444444, 0x323232);
        this.scene.add(this.grid);

        const geometry = new THREE.PlaneGeometry(sizeGrid, sizeGrid);
        const materialG = new THREE.MeshBasicMaterial({color: 0x010101, side: THREE.DoubleSide, 
            transparent: true, opacity: 0
        });
        this.ground = new THREE.Mesh(geometry, materialG);
        this.ground.rotation.x = -Math.PI / 2; // Rotate to make it flat
        // this.ground.position.set(0,-1,0)
        this.scene.add(this.ground);

        
        // @ts-ignore
        const material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
        const geometrySphere = new THREE.SphereGeometry(0.05, 50, 50);
        const objectSphere = new THREE.Mesh(geometrySphere, material);
        objectSphere.translateX(1)
        this.sphere = objectSphere
        this.scene.add(objectSphere);


        // let geometry = new THREE.SphereGeometry(1, 50, 50);
        // let object = new THREE.Mesh(geometry, new THREE.MeshPhongMaterial());
        // object.translateX(-1)
        // this.add(object);

        // geometry = new THREE.SphereGeometry(1, 50, 50);
        // object = new THREE.Mesh(geometry, new THREE.MeshPhongMaterial());
        // object.translateX(1)
        // this.add(object);
    }
    public reset() {
        this.arrayMesh = [];
        this.arrayPoint = [];
        this.arrayArrow = [];

        for( let i = this.scene.children.length - 1; i >= 0; i--) { 
            let obj = this.scene.children[i];
            this.scene.remove(obj); 
       }
       this.initPost()
    }
    set showGrid(v: boolean) {
        this.grid.visible = v;
    }
    set showAxis(v: boolean) {
        this.axis.visible = v;
    }

    public add(object: THREE.Mesh, point, picking = true) {
        this.scene.add(object);
        if (picking) {
            this.arrayMesh.push(object)
            this.arrayPoint.push(point)
        }
    }
    public addArrow(vec, velocity) {
        const arrowHelper = new THREE.ArrowHelper( velocity, vec, 1, '#f00' );
        this.arrayArrow.push(arrowHelper)
        this.scene.add( arrowHelper );
    }
    public addPoint(v: any) {
        // @ts-ignore
        const material = new THREE.MeshBasicMaterial({ color: 0xffffff });
        const geometry = new THREE.SphereGeometry(0.1, 20, 20);
        const object = new THREE.Mesh(geometry, material);
        object.translateX(v.vec.x);
        object.translateY(v.vec.z);
        object.translateZ(v.vec.y);
        this.add(object, v);
        this.addArrow(v.vec, v.velocity);

        
    }
    public mouseMove(e: MouseEvent): void {
        console.log('mouseMove')
        this.picking(e)

    }
    public picking(e: MouseEvent) {
        this.mouse.x = (e.offsetX / this.renderer.domElement.clientWidth) * 2 - 1;
        this.mouse.y = -(e.offsetY / this.renderer.domElement.clientHeight) * 2 + 1;

        this.raycaster.setFromCamera(this.mouse, this.camera);

        const intersects = this.raycaster.intersectObjects([ ...this.arrayMesh]);
        if (intersects.length > 0) {

            // console.log(intersects[0].object);
            console.log(intersects[0].point.x, intersects[0].point.y, intersects[0].point.z)
            this.m.set(intersects[0].point.x, intersects[0].point.y, intersects[0].point.z);

            if (intersects[0].object !== this.sphere) {
                this.sphere.position.set(intersects[0].point.x, intersects[0].point.y, intersects[0].point.z)
            }
            return intersects;

        }

        const intersectGround = this.raycaster.intersectObjects([this.ground]);
        if (intersectGround.length > 0) {
            // console.log(intersectGround)
            this.m.set(intersectGround[0].point.x, intersectGround[0].point.y, intersectGround[0].point.z);
            this.sphere.position.set(intersectGround[0].point.x, intersectGround[0].point.y, intersectGround[0].point.z)
        }

        return undefined;
    }
    public mouseClick(e: MouseEvent): void {
        console.log('mouseClick')
        const mesh = this.picking(e)
        if (mesh) {
            console.log({mesh})
        }
        console.log('mouseClick')
        this.mouseClickPost(e);
    }

    public update(scene: THREE.Scene) {
        this.render(scene)

        this.arrayMesh.forEach((m: THREE.Mesh, i) => {
            m.position.x = this.arrayPoint[i].vec.x;
            m.position.y = this.arrayPoint[i].vec.z;
            m.position.z = this.arrayPoint[i].vec.y;
        })

        this.arrayArrow.forEach((m: THREE.ArrowHelper, i) => {
            m.position.x = this.arrayPoint[i].vec.x;
            m.position.y = this.arrayPoint[i].vec.z;
            m.position.z = this.arrayPoint[i].vec.y;
            
            const vec = new THREE.Vector3(this.arrayPoint[i].velocity.x, this.arrayPoint[i].velocity.z, this.arrayPoint[i].velocity.y)
            const len = vec.length() * 0.5
            m.setLength(len, 0.1,  0.1)
            m.setDirection(vec)
        })

        const matrix = new THREE.Matrix4();
        matrix.makeRotationY(Math.PI * 0.011);
        this.pointLight.position.applyMatrix4(matrix);

    }
    public render = (scene: THREE.Scene) => { }
    public mouseClickPost = (e: MouseEvent) => {
        // console.log('mouseClick, implementation needed')
    }
}
